body {
  width: 100vw;
  height: 100vh;
  letter-spacing: .2em;
  background-color: #FDFDFD; }

.mainpage {
  overflow: hidden;
  height: 100vh; }
  .mainpage:hover {
    overflow-y: scroll; }

.navbar {
  background-color: #fff !important;
  position: fixed;
  width: 100vw !important;
  z-index: 999;
  -webkit-transition: all .8s ease;
  -o-transition: all .8s ease;
  transition: all .8s ease; }

/*----------------------------- home ------------------------------*/
footer {
  font-family: 'Abhaya Libre';
  width: 100vw;
  height: 30vh !important;
  font-size: 1.6em;
  background-color: #ddd;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  color: #777; }

.home-header {
  position: relative;
  width: 100vw !important;
  height: 100vh !important;
  background-size: cover;
  background-position: -100px 0;
  background-repeat: no-repeat;
  text-align: center; }
  .home-header > video {
    opacity: 0.8;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0; }
  .home-header > .header-text {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff; }
    .home-header > .header-text h1 {
      font-size: 6em; }
    .home-header > .header-text h2 {
      font-size: 5em;
      font-family: 'Bilbo Swash Caps'; }

.home {
  text-align: center;
  background-color: #FDFDFD !important; }
  .home > .home-polls {
    padding: 5%;
    min-height: 100vh;
    font-family: 'Abhaya Libre'; }
    .home > .home-polls > .header-text {
      padding: 4% !important; }
      .home > .home-polls > .header-text > h2 {
        font-family: 'Open+Sans' !important;
        color: #753 !important;
        font-size: 3em !important; }

.cardsGrid {
  background-color: #FDFDFD !important;
  padding: 2%; }

.cardsRow {
  -webkit-column-count: 3;
  -webkit-column-gap: .2%;
  -webkit-column-fill: auto;
  -moz-column-count: 3;
  -moz-column-gap: .2%;
  -moz-column-fill: auto;
  column-count: 3;
  column-gap: .2%;
  column-fill: auto;
  grid-auto-flow: dense; }

.card {
  -webkit-break-inside: avoid-column !important;
  -moz-break-inside: avoid-column !important;
  -webkit-column-break-inside: avoid !important;
          break-inside: avoid-column !important;
  width: 100% !important;
  background: #e5e0d5 !important; }

.header {
  color: #444 !important;
  font-family: 'Abhaya Libre' !important;
  font-size: 1.5em !important; }

.drop {
  background-color: blue; }

.menu {
  margin: 0 !important; }

/*----------------------------- pollsPage ------------------------------*/
.accordion .title {
  -webkit-transition: background 0.1s ease, color 0.1s ease;
  -o-transition: background 0.1s ease, color 0.1s ease;
  transition: background 0.1s ease, color 0.1s ease; }

.pollsPage {
  height: 100vh; }
  .pollsPage .welcome_class {
    padding: 2em;
    color: #594040 !important; }

.createdAt {
  float: right !important; }

.myPollsView {
  max-height: 90vh !important;
  border: 1px solid #eee;
  overflow: scroll;
  display: tabel-cell !important;
  vertical-align: middle !important; }

.pollsAcordion {
  width: 100% !important;
  max-height: 100% !important; }

.pollQuestion {
  word-wrap: break-word;
  white-space: nowrap;
  max-width: 520px;
  overflow: hidden !important;
  -o-text-overflow: ellipsis !important;
     text-overflow: ellipsis !important; }

/*----------------------------- Modal ------------------------------*/
.dimmer {
  background-color: rgba(0, 0, 0, 0.3) !important; }

.modalHeader {
  background-color: #2b2b2b !important;
  color: white !important;
  font-family: 'Abhaya Libre' !important; }

.ui.button {
  color: #711 !important; }

.modalContent {
  color: #444 !important;
  font-family: 'Abhaya Libre' !important; }
  .modalContent > h3 {
    font-family: 'Abhaya Libre' !important; }

.selection {
  color: #9b8686 !important;
  font-size: 1.3em !important;
  font-family: 'Abhaya Libre' !important; }



